import React from 'react';
import styles from '../styles/About.module.css';

const About = () => (
  <section id="about" className={styles.section}>
    <div className={styles.container}>
      <p className={styles.eyebrow}>Who we are</p>
      <h2>Building futuristic experiences from Kerala</h2>
      <p className={styles.copy}>
        3Dr Axis Labs is a multidisciplinary studio that blends robotics, FPV cinematics, rapid manufacturing, and
        full-stack software. Our team prototypes, flies, and deploys products that feel like they arrived from the
        futureyet ship on time for todays launches.
      </p>
      <div className={styles.stats}>
        <div>
          <strong>50+</strong>
          <span>client deliveries</span>
        </div>
        <div>
          <strong>5+</strong>
          <span>domains mastered</span>
        </div>
        <div>
          <strong>9.6</strong>
          <span>avg. NPS</span>
        </div>
      </div>
    </div>
  </section>
);

export default About;

