import React from 'react';
import styles from '../styles/Contact.module.css';

const Contact = () => (
  <section id="contact" className={styles.section}>
    <div className={styles.container}>
      <div>
        <p className={styles.eyebrow}>Lets collaborate</p>
        <h2>Tell us about your mission</h2>
        <p className={styles.copy}>
          Whether you need high-speed aerial coverage, robotics expertise, or a polished digital experience, 3Dr Axis
          Labs can help you deliver it faster.
        </p>
        <ul className={styles.list}>
          <li>Email: <a href="mailto:3draxislabs@gmail.com">3draxislabs@gmail.com</a></li>
          <li>Phone: <a href="tel:+917483482350">+91 74834 82350</a></li>
        </ul>
      </div>
      <form className={styles.form}>
        <input type="text" placeholder="Name" required />
        <input type="email" placeholder="Email" required />
        <textarea rows="4" placeholder="Project details" required />
        <button type="submit">Send</button>
      </form>
    </div>
  </section>
);

export default Contact;

